'use client';

import Header from '../components/header/page';
import Footer from '../components/footer/page';
import { useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const WomensPage: React.FC = () => {
  useEffect(() => {
    const styleSheet = document.createElement('style');
    styleSheet.textContent = `
      .animate-scroll {
        display: inline-block;
        white-space: nowrap;
      }
      .highlight-grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr); /* Fixed 4 columns */
        grid-template-rows: repeat(4, auto); /* Fixed 4 rows */
        gap: 20px;
        padding: 20px;
      }
      .highlight-card {
        position: relative;
        overflow: hidden;
        border-radius: 8px;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        display: flex;
        align-items: center;
        justify-content: center;
        height: 250px; /* Uniform height for consistency */
      }
      .highlight-card img {
        width: 100%;
        height: auto;
        max-height: 100%; /* Ensure image fits within height */
        object-fit: contain; /* Show entire image, maintain aspect ratio */
        transition: transform 0.3s ease;
      }
      .highlight-card:hover img {
        transform: scale(1.05);
      }
      .highlight-text {
        position: absolute;
        bottom: 10px;
        left: 10px;
        color: white;
        z-index: 1;
      }
    `;
    document.head.appendChild(styleSheet);

    return () => {
      document.head.removeChild(styleSheet);
    };
  }, []);

  const featuredSections = [
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/415060f1_6b21.jpg', text: 'Featured 1', button: 'Shop Now', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/5cde1622_a4e5.jpg', text: 'Featured 2', button: 'Read More', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://www.madewell.com/brand_creative/mw-jpg/2025/w-2025-jun-live06-17-hp-img3.jpg?wid=1000', text: 'Signs of Summer', button: 'Shop Now', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://www.madewell.com/brand_creative/mw-jpg/2025/w-2025-jun-live06-17-hp-img4.jpg?wid=1000', text: 'Dress in Moments', button: 'Shop Now', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/081cf835_6ee5.jpg', text: 'Featured 3', button: 'Read More', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/e8869f36_1cae.jpg', text: '', button: '', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    // Repeat entries to fill 4x4 grid (16 items)
    { image: 'https://scontent-sea1-1.cdninstagram.com/v/t51.29350-15/381349243_219166864477701_2722604095478980898_n.jpg?stp=dst-jpg_e35_tt6&_nc_cat=111&ccb=1-7&_nc_sid=18de74&_nc_ohc=oXHSDSVhE8gQ7kNvwFQYNda&_nc_oc=AdnmeurnVZmdSaDXtjob-75GAysohinN06S4o982mftjuSPWQsiiDzSKa85df843s5k&_nc_zt=23&_nc_ht=scontent-sea1-1.cdninstagram.com&edm=ANo9K5cEAAAA&_nc_gid=o9LGIJxgoCJ5E0BRMiF2qQ&oh=00_AfPIzTsLpShLU8pOEqR6iowQY_ajlCmISZALQi2izeZfxg&oe=685AA05D', text: 'Featured 1', button: 'Shop Now', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://scontent-sea1-1.cdninstagram.com/v/t51.29350-15/394283655_990915805348153_8486392269937084259_n.jpg?stp=dst-jpg_e35_tt6&_nc_cat=110&ccb=1-7&_nc_sid=18de74&_nc_ohc=I98heUSF6JcQ7kNvwGBG_7L&_nc_oc=Adk-EhQrjlklp9lhK1jN_ODgIqLvoFKLqEeSk1glqGEnV3qGzklqhWDWSEWulwvlSLM&_nc_zt=23&_nc_ht=scontent-sea1-1.cdninstagram.com&edm=ANo9K5cEAAAA&_nc_gid=o9LGIJxgoCJ5E0BRMiF2qQ&oh=00_AfOXRe3c4V8O1XeMSzFkyoGiNKKbg9PwceAvZRxKl2eNjA&oe=685A99F8', text: 'Featured 2', button: 'Read More', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://scontent-sea1-1.cdninstagram.com/v/t51.29350-15/458456857_868388638586578_5621894333613731522_n.jpg?stp=dst-jpg_e35_tt6&_nc_cat=108&ccb=1-7&_nc_sid=18de74&_nc_ohc=EQbjcAeODTAQ7kNvwEPmo9V&_nc_oc=Adm83XSgxzsQfVMJ6gxscR_Z2EQTykTaVQ3ew960cenXbrX2miSCRbO2JjJ_ABTrvfc&_nc_zt=23&_nc_ht=scontent-sea1-1.cdninstagram.com&edm=ANo9K5cEAAAA&_nc_gid=o9LGIJxgoCJ5E0BRMiF2qQ&oh=00_AfNMtvJIda1nEU9aWFLxdB4-RynHbbALqRn0IQmRbyA5jg&oe=685A9553', text: 'Featured 1', button: 'Shop Now', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://static.wixstatic.com/media/486bd5_bc4291aff6d248edb4ee86516a98a52d~mv2.jpg/v1/fill/w_614,h_820,al_c,q_85,usm_0.66_1.00_0.01,enc_avif,quality_auto/486bd5_bc4291aff6d248edb4ee86516a98a52d~mv2.jpg', text: 'Featured 2', button: 'Read More', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://static.wixstatic.com/media/486bd5_5f1d4aeef5f54ee490a108f23f41a414~mv2.jpg/v1/fill/w_614,h_820,al_c,q_85,usm_0.66_1.00_0.01,enc_avif,quality_auto/486bd5_5f1d4aeef5f54ee490a108f23f41a414~mv2.jpg', text: 'Signs of Summer', button: 'Shop Now', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
    { image: 'https://static.wixstatic.com/media/486bd5_590b2a213a074039997560b877947e51~mv2.jpg/v1/fill/w_614,h_820,al_c,q_85,usm_0.66_1.00_0.01,enc_avif,quality_auto/486bd5_590b2a213a074039997560b877947e51~mv2.jpg', text: 'Dress in Moments', button: 'Shop Now', link: 'https://www.everlane.com/collections/womens-vacation-shop' },
  ];

  const sustainabilitySection = {
    image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/c3a02f7c_631f.png',
    text: "Sustainability Looks Better With Receipts. Here's Ours.",
    button: 'Read Our Impact Report',
    link: 'https://www.everlane.com/collections/womens-vacation-shop',
  };

  const categories = [
    { name: 'SHOP TEES', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/1650930e_5f1b.jpg', link: '/womens/dresses' },
    { name: 'SHOP PANTS', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/0f25034f_96fa.jpg', link: '/womens/tops' },
    { name: 'SHOP DENIM', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/1996bc0b_2b59.jpg', link: '/womens/pants' },
    { name: 'SHOP DRESSES', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/74db686e_e178.jpg', link: '/womens/skirts' },
    { name: 'SHOP TOPS', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/f9db9668_494e.jpg', link: '/womens/outerwear' },
    { name: 'SHOP SHOES', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_1000/v1/i/53a91526_d443.jpg', link: '/womens/accessories' },
  ];

  return (
    <div className="min-h-screen bg-stone-50 text-stone-900 font-serif">
      <Header />
      <main>
        {/* Hero Video Section */}
        <section className="relative w-full h-[60vh] overflow-hidden">
          <video
            className="absolute top-0 left-0 w-full h-full object-cover"
            src="/images/main.mp4"
            autoPlay
            loop
            muted
            playsInline
          />
          <div className="absolute inset-0 bg-teal-900/40 flex items-center justify-center">
            <div className="text-center text-white p-4">
              <h1 className="text-3xl md:text-4xl font-extralight mb-2">Women’s Collection</h1>
              <p className="text-md md:text-lg">Sustainable elegance for every day.</p>
            </div>
          </div>
        </section>

        {/* Categories Grid with Framer Motion */}
        <section className="py-16 bg-white overflow-hidden">
          <div className="max-w-3xl mx-auto px-4">
            <h2 className="text-xl md:text-2xl font-medium text-center mb-8">Browse Our Categories</h2>
            <div className="overflow-hidden">
              <motion.div
                className="animate-scroll"
                animate={{ x: '-33.333%' }}
                transition={{ duration: 50, ease: 'linear', repeat: Infinity }}
              >
                <AnimatePresence>
                  {[...categories, ...categories, ...categories].map((category, index) => (
                    <motion.a
                      key={index}
                      href={category.link}
                      className="inline-block p-2 w-96"
                      initial={{ opacity: 1 }}
                      animate={{ opacity: 1 }}
                      exit={{ opacity: 1 }}
                      transition={{ duration: 0 }}
                    >
                      <div className="relative h-120 rounded-lg shadow-md overflow-hidden">
                        <img
                          src={category.image}
                          alt={category.name}
                          className="w-full h-full object-cover"
                        />
                        <span className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-black text-xl font-semibold uppercase bg-white bg-opacity-70 p-4 rounded-lg">
                          {category.name}
                        </span>
                      </div>
                    </motion.a>
                  ))}
                </AnimatePresence>
              </motion.div>
            </div>
          </div>
        </section>

        {/* Featured Highlights with 4x4 Grid Layout */}
        <section className="py-16 bg-stone-100">
          <div className="max-w-6xl mx-auto px-4">
            <h2 className="text-xl md:text-2xl font-medium text-center mb-8">Highlight Picks</h2>
            <div className="highlight-grid">
              {featuredSections.slice(0, 16).map((section, index) => (
                <a
                  key={index}
                  href={section.link}
                  className="highlight-card"
                >
                  <img
                    src={section.image}
                    alt={section.text || `Highlight ${index + 1}`}
                    className="highlight-card"
                  />
                  {section.text && (
                    <div className="highlight-text">
                      <h3 className="text-md md:text-lg font-medium uppercase">{section.text}</h3>
                      <span className="text-xs md:text-sm uppercase">{section.button}</span>
                    </div>
                  )}
                </a>
              ))}
            </div>
          </div>
        </section>

        {/* Sustainability Focus */}
        <section className="py-16 bg-white">
          <a
            href={sustainabilitySection.link}
            className="relative block w-full"
          >
            <img
              src={sustainabilitySection.image}
              alt="Sustainability"
              className="w-full h-80 md:h-120 object-cover"
            />
            <div className="absolute inset-0 bg-teal-900/50 flex items-center justify-center">
              <div className="text-center text-white p-4">
                <h3 className="text-lg md:text-xl font-light">{sustainabilitySection.text}</h3>
                <span className="text-xs md:text-sm uppercase mt-1 inline-block">{sustainabilitySection.button}</span>
              </div>
            </div>
          </a>
        </section>

        {/* Additional Content */}
        <section className="py-16 bg-stone-100 text-center">
          <h2 className="text-xl md:text-2xl font-medium mb-4">Discover More</h2>
          <p className="text-md md:text-lg text-teal-800 mb-6">Explore our latest arrivals and exclusive offers.</p>
          <a href="/womens/explore" className="inline-block bg-teal-600 text-white font-medium py-2 px-4 rounded hover:bg-teal-700 transition-colors">
            Explore Now
          </a>
        </section>
      </main>
      <Footer />
    </div>
  );
};

export default WomensPage;